/* ===== Legacy Browser ===== */

.legacy {
	display: table;
	position: fixed;
	bottom: 0;
	left: 0;
	z-index: 999;
	width: 100%;
	min-height: 100px;
	padding: 5px 60px;
	background: $color_primary;
	font-size: 14px;
	line-height: 2em;
	color: $color_bg;
	text-align: center;
	.inner {
		display: table-cell;
		vertical-align: middle;
		a {
			font-size: 16px;
			font-weight: 700;
			color: #fff;
		}
	}
	button {
		overflow: hidden;
		position: absolute;
		top: 20px;
		right: 20px;
		width: 30px;
		height: 30px;
		background: url("data:image/png;base64,");
		text-indent: -9999px;
	}
}

/* ===== Header ===== */

header {
	margin-top: 50px;
	padding: 0.5em 0;
	font-size: 20px;
	text-align: center;
	span {
		margin-left: 0.5em;
		color: $color_primary;
		@include themify("dark") {
			color: map-deep-get($themes, "dark", "color_primary");
		}
	}
	@include screen("tablet") {
		font-size: 18px;
		margin-top: 30px;
	}
	@include screen("mobile") {
		font-size: 16px;
		margin-top: 15px;
	}
}
